import useAuthAPI from "hooks/API/useAuthAPI";
import { idReg, nameReg, pwdReg } from "assets/regExp/authRegExp";
import Modal from "components/Modal";
import { AuthButton } from "components/common/styleObject/ButtonStyle";
import useModal from "hooks/modal";
import { useRef, useState } from "react";
import { useMutation } from "react-query";
import { useNavigate } from "react-router-dom";
import AuthForm from "../AuthForm";
import AuthInput from "../AuthInput";

const Join = () => {
  const [id, setId] = useState("");
  const [pwd, setPwd] = useState("");
  const [name, setName] = useState("");

  const { Join } = useAuthAPI();
  const navigate = useNavigate();

  const { mutate } = useMutation(() => Join(id, pwd, name), {
    onSuccess: () => navigate("/login"),
    // onError: showModal,
  });

  return (
    <>
      <AuthForm submit={() => (id && pwd && name ? mutate() : mutate())}>
        <>
          <AuthInput
            setValue={setId}
            type="text"
            placeholder=""
            reg={idReg}
            errMsg="6~20     ."
            successMsg="   ."
          />
          <AuthInput
            setValue={setPwd}
            type="password"
            placeholder=""
            reg={pwdReg}
            errMsg="8~16     "
            successMsg="   ."
          />
          <AuthInput
            setValue={setName}
            type="text"
            placeholder=""
            reg={nameReg}
            errMsg="8~16     "
            successMsg="   ."
          />
          <div>
            <AuthButton></AuthButton>
            <AuthButton onClick={() => navigate("/login")}>
                
            </AuthButton>
          </div>
        </>
      </AuthForm>
      {/* <Modal
        isModal={isModal}
        title={" Error "}
        buttonText={""}
        btnClick={closeModal}
      >
        <>
          {status === 1 && "    "}
          {status === 500 && " "}
        </>
      </Modal> */}
    </>
  );
};

export default Join;
