import useAuthAPI from "hooks/API/useAuthAPI";
import { idReg, pwdReg } from "assets/regExp/authRegExp";
import axios, { AxiosError } from "axios";
import Modal from "components/Modal";
import { AuthButton } from "components/common/styleObject/ButtonStyle";
import useModal from "hooks/modal";
import { useState } from "react";
import { QueryClient, useMutation, useQueryClient } from "react-query";
import { useNavigate } from "react-router-dom";
import AuthForm from "../AuthForm";
import AuthInput from "../AuthInput";

const Login = () => {
  const [id, setId] = useState("");
  const [pwd, setPwd] = useState("");

  const navigate = useNavigate();
  const { Login } = useAuthAPI();

  const quetClient = useQueryClient();
  const { openModal } = useModal();

  const { mutate } = useMutation(() => Login(id, pwd), {
    onSuccess: (data) => {
      const { token } = data?.data.data;
      localStorage.setItem("AccessToken", token);
      axios.defaults.headers.common["Authorization"] = token;
      quetClient.refetchQueries("myInfo");
      navigate("/");
    },
    onError: (error: AxiosError) => {
      openModal("error", { status: error.response?.status });
    },
  });

  return (
    <>
      <AuthForm submit={() => (id && pwd ? mutate() : mutate())}>
        <>
          <AuthInput
            setValue={setId}
            placeholder=""
            type="text"
            reg={idReg}
            errMsg="6~20     "
          />
          <AuthInput
            setValue={setPwd}
            placeholder=""
            type="password"
            reg={pwdReg}
            errMsg="8~16     "
          />
          <div>
            <AuthButton></AuthButton>
            <AuthButton onClick={() => navigate("/join")}>
                
            </AuthButton>
          </div>
        </>
      </AuthForm>
      {/* <Modal
        isModal={isModal}
        title={" Error "}
        buttonText={""}
        btnClick={closeModal}
        backgroundClick={closeModal}
      >
        <>
          {status === 400 && " "}
          {status === 404 && "   "}
          {status === 1 && "    "}
          {status >= 500 && " "}
          {status === 0 && "   ."}
        </>
      </Modal> */}
    </>
  );
};

export default Login;
